@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    height: 100%;
    display: flex;
    flex-direction: column;
    &__header {
        background-color: v.$negro;
        margin: 0;
        display: flex;
        padding-left: 1rem;
        &__logo {
            padding: 1rem;
        }
        &__inicio {
            all: unset;
            color: v.$blanco;
            margin-top: 1.5rem;
            @include m.telefono {
                display: none;
            }
            &:hover {
                cursor: pointer;
            }
        }
        &__barra-superior {
            flex: 1;
            display: flex;
            justify-content: space-around;
            align-items: center;
        }
        &__texto {
            color: v.$blanco;
            span {
                color: darken(v.$verde, 10);
            }
        }
        &__logout {
            all: unset;
            color: v.$blanco;
            &:hover {
                cursor: pointer;
                color: v.$verde;
            }
        }
    }
    &__grid {
        display: flex;
        flex: 1;
        overflow-y: auto;
    }
    &__sidebar {
        background-color: lighten(v.$negro, 20);
        width: auto;
        text-align: center;
        @include m.tablet {
            padding: 0 3rem;
        }
        display: flex;
        flex-direction: column;
        &__enlace {
            all: unset;
            &:hover {
                cursor: pointer;
            }
        }
        &__imagen {
            width: 4rem;
            @include m.tablet {
                width: 8rem;
                margin: 2rem 0 0 0;
            }
        }
        span {
            font-weight: v.$bold;
            font-size: 1.2rem;
            display: block;
            @include m.tablet {
                font-size: 2rem;
            }
        }
    }
    &__contenido {
        width: 100%;
        padding: 1rem 1rem 3rem 1rem;
        overflow-y: scroll;
        @include m.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }
}

.admin {
    text-align: center;
    &__imagen {
        width: 20rem;
        height: 20rem;
        margin: 1.5rem 0;
    }
}