@use 'base/variables' as v;
@use 'base/mixins' as m;

.redes {
    color: v.$verde;
}

.menu-redes {
    @include m.menu-social(v.$negro, 3rem);
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin: 4rem 0;
    @include m.tablet {
        justify-content: center;
    }
    &__enlace {
        padding: 1rem;
        background-color: v.$verde;
        border-radius: 50%;
    }
}