@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    position: relative;
    background-color: v.$negro;
    &__contenedor {
        @include m.contenedor;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__logout {
        all: unset;
        color: v.$verde;
        &:hover {
            cursor: pointer;
            color: v.$blanco;
        }
    }
    &__logo {
        width: 12rem;
        height: 12rem;
        margin: 0;
        padding: 0;
        display: none;
        @include m.telefono {
            display: inline;
        }
    }
    &__navegacion {
        margin: 1rem 0;
        display: flex;
        gap: 2rem;
        flex-direction: column;
        text-align: center;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__enlace {
        all: unset;
        color: v.$verde;
        &:hover {
            color: v.$blanco;
        }
        &--logo {
            @extend .header__enlace;
            display: inline;
            margin-top: 1rem;
            @include m.telefono {
                display: none;
            }
        }
        &--carrito {
            width: 3rem;
            height: 3rem;
        }
        &:hover {
            cursor: pointer;
        }
    }
}

.carrito-productos {
    background-color: v.$blanco;
    width: auto;
    position: absolute;
    right: 0;
    z-index: 10;
    margin: 0;
    text-align: center;
    @include m.telefono {
        margin: -1rem 2rem;
    }
    border-radius: .5rem;
    border: 3px solid v.$verde;
    &__titulo {
        color: v.$negro;
        text-align: center;
        padding: 0 1rem;
    }
}
.producto-carrito {
    text-align: center;
    margin: 1rem;
    padding: 1rem;
    thead {
        tr {
            th {
                border-top: 1px solid v.$negro;
                border-bottom: 1px solid v.$negro;
                padding: 1rem;
            }
        }
    }
    tbody {
        tr {
            td {
                border-top: 1px solid v.$negro;
                border-bottom: 1px solid v.$negro;
                padding: 0;
            }
        }
    }
    &__imagen {
        height: 6rem;
        width: 6rem;
    }
    &__precio {

    }
    &__quitar {
        color: v.$rojo;
        &:hover {
            cursor: pointer;
            color: darken(v.$rojo, 30);
        }
    }
}
.ocultar {
    display: none;
}
.no-mostrar {
    display: none;
    @include m.telefono {
        display: block;
    }
}

.vaciar_carrito {
    @include m.boton(v.$verde, v.$negro);
    width: 100%;
    margin: 1rem;
    border: 1px solid v.$negro;
    @include m.tablet {
        width: auto;
        text-align: center;
    }
}