@use 'base/variables' as v;
@use 'base/mixins' as m;

.admin-productos {
    @include m.contenedor-md;
    text-align: right;
    &__titulo {
        text-align: center;
    }
    &__no-productos {
        text-align: center;
    }
    &__enlace {
        @include m.boton(v.$negro, v.$blanco);
        margin-top: 3rem;
        width: auto;
    }
}