@use 'base/variables' as v;
@use 'base/mixins' as m;

.admin-usuarios {
    @include m.contenedor;
    text-align: center;
}

.tabla {
    margin: 0 auto;
    border-collapse: collapse;
    border: 1px solid v.$negro;
    &__thead {
        background-color: lighten(v.$negro, 50);
        text-align: center;
        @include m.tablet {
            &--tr {
                th {
                    border: 1px solid v.$negro;
                    padding: 2rem 4rem;
                    margin: 0;
                }
            }
            &--usuario {
                th {
                    border: 1px solid v.$negro;
                    padding: 2rem 8rem;
                    margin: 0;
                }
            }
        }
    }
    &__tbody {
        &--tr {
            td {
                border: 1px solid v.$negro;
                text-align: center;
                margin: 0 5rem;
            }
        }
        &--imagen {
            height: 5rem;
            width: 5rem;
            margin: 0 1rem;
        }
    }
    &__actualizar {
        padding: 1rem;
        color: v.$verde;
        margin: .5rem 0;
        width: auto;
        display: block;
        &:hover {
            color: lighten(v.$verde, 40);
        }
    }
    &__eliminar {
        all: unset;
        color: v.$rojo;
        padding: 1rem;
        margin: .5rem 0;
        width: auto;
        button {
            all: unset;
        }
        display: block;
        &:hover {
            cursor: pointer;
            color: lighten(v.$rojo, 40);
        }
    }
}