@use 'base/variables' as v;
@use 'base/mixins' as m;

.enlaces {
    margin-top: -1rem;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }
    &__enlace {
        all: unset;
        margin-bottom: 1.5rem;
        display: block;
        &:hover {
            color: v.$verde;
            cursor: pointer;
        }
    }
}