@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$negro;

    &__grid {
        display: flex;
        flex-direction: column;
        text-align: center;
    }

    &__contenido {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
        @include m.contenedor-md;
        @include m.tablet {
            flex-direction: row;
        }
    }

    &__texto {
        color: v.$verde;
    }
}