@use 'base/variables' as v;
@use 'base/mixins' as m;

.productos-individual {
    background-color: v.$negro;
    text-align: center;
    margin: 2rem 0;
    &__titulo {
        color: v.$verde;
        padding: 2rem;
    }
}

.producto-individual {
    padding: 2rem;
    width: auto;
    margin-bottom: 2rem;
    &__nombre {
        color: v.$verde;
    }
    &__imagen {
        width: 17rem;
        height: 17rem;
    }
    &__descripcion {
        color: lighten(v.$verde, 30%);
        margin: 2rem 0 0 0;
    }
    &__precio {
        color: v.$verde;
    }
    &__enlace {
        margin-bottom: 2rem;
        @include m.boton(v.$verde, v.$negro);
        @include m.tablet {
            width: auto;
        }
    }
}