@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros {
    &__mensaje {
        text-align: center
    }
}

.formulario {
    @include m.contenedor-md;

    &__fieldset {
        margin: 4rem 0;
    }
    &__legend {
        font-weight: v.$bold;
    }
    &__campo {
        margin: 1rem 0;
        display: flex;
        align-items: center;
    }
    &__label {
        margin-right: 1rem;
    }
    &__input {
        padding: .8rem;
        flex: 1;
    }
    &__submit {
        @include m.boton(v.$verde, v.$negro);
        margin-bottom: 2rem;
        border: 1.5px solid v.$negro;
        @include m.tablet {
            width: auto;
        }
        &--volver {
            @include m.boton(v.$azul, v.$negro);
            margin-bottom: 2rem;
            border: 1.5px solid v.$negro;
            @include m.tablet {
                width: auto;
            }
        }
    }
}

.botones {
    display: flex;
    justify-content: space-between;
    align-items: center;
}