@use 'base/variables' as v;
@use 'base/mixins' as m;

.banners {
    margin: 1rem 0;
    &__contenido {
        display: flex;
        align-items: center;
        text-align: center;
    }
    &__imagen {
        width: 100%;
    }
}

.productos {
    background-color: v.$negro;
    text-align: center;
    margin: 2rem 0;
    &__titulo {
        color: v.$verde;
        padding: 2rem;
    }
    &__grid {
        margin: 2rem;
        @include m.grid(1, 4rem);
        @include m.tablet {
            @include m.grid(2, 4rem);   
        }
        @include m.desktop {
            @include m.grid(3, 4rem);   
        }
    }
    &__todos {
        @include m.boton(v.$verde, v.$negro);
        margin: 2rem;
        @include m.tablet {
            width: auto;
        }
    }
}

.producto {
    padding: 2rem;
    border: 1px solid v.$verde;
    margin-bottom: 2rem;
    &__nombre {
        color: v.$verde;
    }
    &__imagen {
        width: 17rem;
        height: 17rem;
    }
    &__descripcion {
        color: lighten(v.$verde, 30%);
        margin: 2rem 0 0 0;
    }
    &__precio {
        color: v.$verde;
    }
    &__enlace {
        @include m.boton(v.$verde, v.$negro);
        @include m.tablet {
            width: auto;
        }
    }
}